<template>
  <div id="container">
    <div id="alert" class="error" :class="subsrip ? '' : 'active'">
      <ion-icon name="warning" class="icon"></ion-icon>
      <div class="title">Вы не оформили подписку!</div>
      <div class="pay-btn" @click="subscripInvoice()">Оформить</div>
    </div>
    <HeaderUserBar />

    <div id="banner">
      <div class="swiper banner-collage">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <img src="../files/banner.png" alt=""/>
            <div class="item-gradient"></div>
            <div class="item-content">
              <div class="item-title">{{ title_1 }}</div>
              <div class="item-price">5000</div>
              <div class="item-currency">{{ desc_1 }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="service">
      <div class="service-title">{{ service }}</div>
      <div class="swiper service-cards">
        <div class="swiper-wrapper">
          <router-link tag="div" to="/qr-code-cashback" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/service-partners.svg" alt="Service Partners">
            </div>
            <div class="service-title">{{ cashback }}</div>
          </router-link>
          <router-link tag="div" to="https://prizes.gamee.com/game-bot/karatekid2" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/service-ticket-game.svg" alt="Service Partners">
            </div>
            <div class="service-title">Ticket Game</div>
          </router-link>
          <router-link tag="div" to="/participation" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/service-participate.svg" alt="Service Partners">
            </div>
            <div class="service-title">{{ participation }}</div>
          </router-link>
          <router-link tag="div" to="/give-a-dream" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/humans.svg" alt="Give a dream">
            </div>
            <div class="service-title">{{ give_a_dream }}</div>
          </router-link>
          <router-link tag="div" style="visibility: hidden;opacity: 0" to="/give-a-dream" class="service-card swiper-slide"></router-link>
        </div>
      </div>
    </div>

    <BottomNavigationMenu active="home" />
  </div>
</template>

<script>
import axios from 'axios'
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import HeaderUserBar from "@/components/HeaderUserBar";

export default {
  name: "Home",
  data: () => ({
    user_id: 0,
    user: null,
    subsrip: null,
    title_1: null,
    desc_1: null,
    title_2: null,
    desc_2: null,
    service: null,
    cashback: null,
    participation: null,
    give_a_dream: null,
  }),
  components: {HeaderUserBar, BottomNavigationMenu},
  async mounted() {
    new Swiper(".banner-collage", {
      effect: "cards",
      grabCursor: true,
      autoplay: {
        delay: 2500,
      },
      slidesPerView: 'auto',
      loop: true,
    });

    new Swiper(".service-cards", {
      watchSlidesProgress: true,
      slidesPerView: 3,
    })
    const telegram = window.Telegram.WebApp;
    const userData = telegram.initDataUnsafe.user;
    const mainButton = telegram.MainButton;
    const backButton = telegram.BackButton;

    mainButton.hideProgress();
    mainButton.hide();
    backButton.hide();

    this.user_id = 5296876609
    // this.user_id = userData.id

    await axios.get('https://server.gold-ticket.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data;
          this.subsrip = response.data.data.subscription;
        })
        .catch(error => console.log(error));

    if (!this.user.agreed) {
      this.$router.push('/terms')
    }

    if (this.user.lang === 'ru') {
      this.title_1 = 'Квартира за';
      this.desc_1 = 'сум';
      this.service = 'Другие сервисы';
      this.cashback = 'Кешбек';
      this.participation = 'Участвовать';
      this.give_a_dream = 'Подри мечту';
    } else {
      this.title_1 = 'Kvartira';
      this.desc_1 = 'so\'m evaziga';
      this.service = 'Boshqa servislar';
      this.cashback = 'Keshbek';
      this.participation = 'Qatnashish';
      this.give_a_dream = 'Orzu ulash';
    }

  },
  methods: {
    async subscripInvoice()  {
      let description;
      if (this.user.lang === 'ru') {
        description = '5,000 УЗС/мес. - Месячное подписка для использовании тикетов'
      } else {
        description = '5,000 UZS/oy - Ticketlardan foydalanish uchun oylik obuna'
      }
      let hash2342 = decodeURIComponent(escape(window.atob('Ym90NTU1Njc3MjY0NjpBQUZGdXlQVVpuYjg4REd0Sm1vUHJPOUJXNUpFZjFyVGpMYw==')));
      let ppt3454 = decodeURIComponent(escape(window.atob('Mzg3MDI2Njk2OkxJVkU6NjM2Y2Y5YjU3NGExMjNkNzA3MWVlNDk4')));
      await axios.post(`https://api.telegram.org/${hash2342}/sendInvoice`, {
        'chat_id': this.user_id,
        'title': 'Gold Ticket Premium',
        'description': description,
        'payload': 'premium',
        'provider_token': ppt3454,
        'currency': 'UZS',
        'max_tip_amount': 1000000000,
        'prices': [
          {
            'label': 'Gold Ticket Premium',
            'amount': 500000
          }
        ],
        'photo_url': 'https://gold-ticket.uz/bot/files/payment.jpg',
        'photo_width': 640,
        'photo_height': 360,
        'need_phone_number': true
      }).then(response => {
        const telegram = window.Telegram.WebApp;
        if (this.user.lang === 'ru') {
          telegram.showPopup({
            'title': 'Успешно',
            'message': 'Бот вам отпрвил счёт для оплаты',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Продолжить"
              }
            ]
          })
        } else {
          telegram.showPopup({
            'title': 'Muvofaqiyatli',
            'message': 'Bot sizga tolov uchun hisob yubordi',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Davom etish"
              }
            ]
          })
        }
      }).catch(error => {
        if (!error.data.ok) {
          const telegram = window.Telegram.WebApp;
          if (this.user.lang === 'ru') {
            telegram.showPopup({
              'title': 'Ошибка!',
              'message': 'Произошла ошибка, повторите попытку больше ',
              'buttons': [
                {
                  'id': 'success',
                  'type': 'ok',
                  'text': "Повторить"
                }
              ]
            })
          } else {
            telegram.showPopup({
              'title': 'Xato!',
              'message': 'Tolo hisibi yaratishda hatolik yuz berdi!\n\n Keyinroq qayta urunib koring',
              'buttons': [
                {
                  'id': 'success',
                  'type': 'ok',
                  'text': "Davom etish"
                }
              ]
            })
          }
        }
      });
    },
  },
}
</script>
